











































































































































import Vue from "vue";
import { createSimpleTransition } from 'vuetify/src/util/helpers';
import { Ripple } from 'vuetify/lib/directives';

const romaCrudTransition = createSimpleTransition('unit-list-transition');

Vue.component('transition-crud', romaCrudTransition);

export default Vue.extend({
    name: "RomaCrud",
    props: {
        canEdit: {
            type: Boolean,
            default: true,
        },
        canAdd: {
            type: Boolean,
            default: true,
        },
        canDelete: {
            type: Boolean,
            default: true,
        },
        title: String,
        data: {
            type: Array,
            required: true,
        },
        headers: {
            type: Array,
            required: true,
        },
        onSave: {
            type: Function,
        },
        onDelete: {
            type: Function,
        },
        defaultItem: {
            type: Object,
        }
    },
    data() {
        return {
            selectedItem: null,
            dialogDeleteUnit: false,
            selected: [],
        };
    },
    methods: {
      addItem () {
          // this.data.push(this.defaultItem);
          if (typeof this.onSave === 'function') {
              this.onSave(this.defaultItem);
          } else {
              this.data.push(this.defaultItem);
          }
      },
      goDelItem(item: object) {
          this.dialogDeleteUnit = true;
          this.selectedItem = item;
      },
      delItem () {
          if (typeof this.onDelete === 'function') {
              this.onDelete(this.selectedItem);
          } else {
              const index = this.data.indexOf(this.selectedItem);
              this.data.splice(index, 1);
          }
          this.dialogDeleteUnit = false;
      }
    },
    directives: {
        Ripple
    }
});
