























































































































































import Vue from "vue";
import { Ripple } from 'vuetify/lib/directives';
import { mask } from 'vue-the-mask';

// import RomaIEs from "@/components/RomaIEs.vue";
import RomaCoverage from "@/components/RomaCoverage.vue";

export default Vue.extend({
    name: "RomaOa",
    props: {
        value: { // Unit
            type: Object,
            required: true
        },
    },
    data() {
        return {
            showActionsDialog: false,
            selectedAll: false,
            unit: null,
            mask: '##:##',
            selectedRows: [],
            expanded: [],
            buttons: [
                {
                    label: 'Cobertura',
                    icon: 'fas fa-history',
                    onClick: () => {
                        this.showActionsDialog = true;
                    },
                },
                {
                    label: 'Actividades',
                    icon: 'fas fa-user-check',
                    onClick: () => {
                        console.log('por aqui voy')
                        // this.showActionsDialog = true
                    },
                },
                {
                    label: 'Alternar activo/inactivo',
                    icon: 'fas fa-times-circle',
                    onClick: () => {
                        this.toggleOa(this.selectedRows);
                    },
                }
            ],
            oaDetails: [
                {name: 'actividades', icon: 'fas fa-user-check', prop: 'activities', color: 'secondary'},
                {name: 'comentarios', icon: 'fas fa-comment', prop: 'comments', color: 'primary'},
            ],
            headers: {
                oas:[
                    {
                        text: '',
                        align: 'left',
                        value: 'id',
                    },
                    { text: 'Nombre', value: 'name' },
                    { text: 'Comentarios', value: 'comments' },
                    { text: '', sortable: false },
                    { text: '', value: 'data-table-expand', sortable: false },
                ],
                ies: [
                    { text: 'ID', value: 'id' },
                    { text: 'Nombre', value: 'name' },
                ],
            },
            headersIEs: [
                { text: 'ID', value: 'id' },
                { text: 'Nombre', value: 'name' },
            ],
        };
    },
    computed: {
        isIndeterminate () {
            let opt = false;
            if (this.selectedRows.length !== this.value.oas.length && this.selectedRows.length !== 0) opt = true;
            return opt
        },
        totalSelectedIEs () {
          let sum = 0;
          if (this.selectedRows.length > 0) {
              sum = this.selectedRows.map(oa => oa.selectedRows.length);
              sum = sum.reduce((a, b) => a + b);
          }

          return sum;
        },
        totalHours () {
            let h= 0;
            let m = 0;
            const hours = this.value.oas.map(item => item.hours);
            for (let i = 0; i < hours.length; i++) {
                let arr = hours[i].split(':');
                h += parseInt(arr[0]);
                m += parseInt(arr[1]);
                h += Math.floor(m / 60) ;
                m = m % 60;
            }

            if (isNaN(h) && isNaN(m)) {
                h = "00";
                m = "00";
            }

            return h + ' Horas - ' + m + ' Minutos';
        },
    },
    watch: {
      selectedRows (value) {
          if (value.length === 0) this.selectedAll = false;
      }
    },
    methods: {
      selectOA ({item, value}) {
          item.selectedRows = value ? item.ies : []; // Selecciona todos los ies de un oa
      },
      selectAllOAs () {
          if (this.selectedAll) {
              this.selectedRows = this.value.oas;
              this.selectedRows.forEach(oa => {
                  oa.selectedRows = oa.ies;
              });
          } else {
              this.selectedRows.forEach(oa => {
                  oa.selectedRows = [];
              });
              this.selectedRows = [];
          }
      },
      selectIE (oa: object, {value}) {
          const index = this.selectedRows.indexOf(oa);
          if (value) {
              if (index === -1) this.selectedRows.push(oa); // agrega a oa seleccionado si se marca un ie
          } else {
              if (oa.selectedRows.length === 1) {
                  this.selectedRows.splice(index, 1); // Si deshabilita el ultimo ie quito el oa de seleccionado
              }
          }
      },
      toggleOa (oas) {
        if (Array.isArray(oas)) {
            oas.forEach((oa) => {
                oa.disabled = !oa.disabled;
                oa.hours = '00:00'; // Reinicio la hora para que no cuente en el total
            });
            this.selectedRows = []; //unselect rows
        } else {
            oas.disabled = !oas.disabled;
        }
        this.$store.commit('setUnit', {unit: this.value});
      },
      rowStyle (itemIndex: number, disabled: boolean) {
          const colors = ['--v-roma_rcolor-base', '--v-primary-base', '--v-secondary-base', '--v-roma_acolor-base'];
          let color = colors[itemIndex];
          color = `var(${colors[itemIndex % colors.length]})`;

          if (disabled) color = 'grey';

          return `border-left: 8px solid ${color};`;
      }
    },
    components: {
        RomaCoverage,
        // 'roma-ies': RomaIEs
    },
    directives: {
        mask,
        Ripple
    }
});
